const app = getApp();
export default {
	data() {
		return {
			default_avatar: '../../static/default-avatar.png',
			alumni_avatar: '',
			alumni_sex: 2,
			male_icon: '../../static/male.png',
			female_icon: '../../static/female.png',
			is_collect: 1,
			user_info: {},
			alumniList: [],
			collected: true,
			collect_none: false
		}
	},
	onLoad() {
		// console.log(app.reToken())
		if(app.reToken()) {
			// console.log(123)
			uni.getStorageSync("access_token")
		}
		this.getUserInfo();
		this.getAlumniList();
	},
	onShow() {
		if(uni.getStorageSync("reload")) {
			this.getAlumniList();
			uni.setStorageSync("reload", false);
		}
		if(uni.getStorageSync("coll_reload")) {
			this.getAlumniList();
			uni.setStorageSync("coll_reload", false);
		}
	},
	methods: {
		getUserInfo() {
			uni.request({
				url: getApp().globalData.url + 'api/user/info',
				method: 'GET',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					// console.log(res)
					this.user_info = res.data.items.info;
					uni.setStorageSync("userPhone", res.data.items.user.mobile)
				},
				fail: (res) => {
				
				}
			})
		},
		getAlumniList() {
			uni.request({
				url: getApp().globalData.url + 'api/user/collect/recent',
				method: 'get',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					// console.log(res)
					this.alumniList = res.data.items
					if(this.alumniList.length > 0) {
						this.collect_none = false
					}else {
						this.collect_none = true
					}
				},
				fail: (res) => {
					
				}
			})
		},
		addCol(id, i) {
			var _this = this;
			var requestCode = uni.request({
				url: getApp().globalData.url + 'api/user/collect',
				method: 'POST',
				data: {
					info_id: id
				},
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					if (res.data.errorCode == 0) {
						uni.showToast({
							icon: 'none',
							title: '取消收藏成功',
						});
						this.getAlumniList()
						uni.setStorageSync("coll_reload", true);
					}
				},
				fail: (res) => {
					console.log(res);
					uni.showToast({
						icon: 'none',
						title: '请求失败，请稍后再试',
					});
				},
			})
		},
		collectList() {
			if(this.collect_none) {
				uni.showToast({
				    icon: 'none',
				    title: '暂无收藏哦',
				});
			}else {
				uni.navigateTo({
					url: './collectList'
				})
			}
		},
		to_info() {
			uni.navigateTo({
				url: './info'
			})
		},
		to_setting() {
			uni.navigateTo({
				url: './account'
			})
		},
		login() {
			uni.navigateTo({
				url: '../login/login'
			})
		},
		logout() {
			uni.showModal({
				title: '提示',
				content: '是否确定退出登录',
				cancelColor: '#BCC9C3',
				confirmColor: '#1DBF74',
				success: function(res) {
					if (res.confirm) {
						var requestCode = uni.request({
							url: getApp().globalData.url + 'api/logout',
							method: 'POST',
							header: {
								Authorization: 'Bearer ' + uni.getStorageSync('access_token')
							},
							success: (res) => {
								// console.log(res)
								if (res.data.errorCode == 0) {
									uni.showToast({
										icon: 'none',
										title: '退出成功',
									});
									uni.setStorageSync('access_token', false)
									uni.reLaunch({
										url: '../login/login'
									})
								}
							},
							fail: (res) => {
								console.log(res);
								uni.showToast({
									icon: 'none',
									title: '请求失败，请稍后再试',
								});
							},
						})
					}
				}
			})
		}
	}
}
